<style scoped>
	.specialsell_wrap{
		width: 1200px;
		margin: 40px auto 0;
	}
	.specialsell_wrap ul.specialsellfood_wrap{
		margin-top: 20px;
	}
	.specialsell_wrap ul.specialsellfood_wrap li.specialsellfood{
		float: left;
		position: relative;
		width: 240px;
		text-align: center;
		margin: 0 30px;
	}
	.specialsell_name_wrap{
		position: absolute;
		top:-7px;
		left: 50%;
		margin-left: -93px;
	}
	.specialsell_name_wrap_left{
		font-size: 50px;
		font-family: Times New Roman;
		margin-right: 126px;
	}
	.specialsell_name_wrap_right{
		font-size: 50px;
		font-family: Times New Roman;
	}
	.specialsell_name{
		text-align: center;
	}
	.specialsell_name_title{
		font-size: 24px;
		font-family: 'Miscrosoft YaHei';
	}
	.specialsell_name_english{
		font-size: 13px;
		color: rgb(51,51,51);
		font-family: 'Miscrosoft YaHei',Arial;
	}
	.specialsell_bottom_name_wrap{
		text-align: center;
	}
	.specialsell_bottom_name{
		color: rgb(102,102,102);
		font-size: 16px;
		font-weight: 700;
		font-family: 'Miscrosoft YaHei';
	}
	.specialsell_price{
		width: 150px;
		height: 41px;
		background-color: rgba(237,73,22,1);
		text-align: center;
		margin-left: 44px;
		margin-top: 22px;
		line-height: 40px;
		border-radius: 50px;
		cursor: pointer;
		transition: 0.3s ease-in-out;
	}
	.specialsell_price span.specialsell_price_text{
		color: #FFF;
		font-family: 'Miscrosoft YaHei';
	}

	/* 鼠标滑过价格文字的背景颜色变化 */
	.specialsell_price:hover{
		transition: 0.3s ease-in-out;
		background-color: #151926;
	}

	.specialsell_img{
		width: 230px;
		height: 219px;
		cursor: pointer;
	}
	.specialsell_img_show{
		width: 230px;
		height: 219px;
	}
	.specialsell_img_hide{
		display: none;
		margin-left: 10px;
		width: 230px;
		height: 219px;
	}
</style>


<template>
	<div class="specialsell_wrap">
      	<toptitle :message="toptitlemessage"></toptitle>
      	<ul class="specialsellfood_wrap">
      		<li
      			class="specialsellfood"
      			v-for="list in todaysellfood">
	          	<div class="specialsell_name_wrap">
	            	<span class="specialsell_name_wrap_left">{</span>
	            	<span class="specialsell_name_wrap_right">}</span>
	          	</div>
	          	<div class="specialsell_name">
	            	<span class="specialsell_name_title">
	            		{{ list.name }}
	            	</span>
	            	<br>
	            	<span class="specialsell_name_english">
	            		{{ list.englishkind }}
	            	</span>
	          	</div>
	          	<div class="specialsell_img">
	            	<img 
	            		class="specialsell_img_show"
	            		:src="list.todaysellfoodshowimg">
	            	<img 
	            		class="specialsell_img_hide"
	            		:src="list.todaysellfoodhideimg">
	          	</div>
	          	<div class="specialsell_bottom_name_wrap">
	            	<span class="specialsell_bottom_name">
	            		{{ list.shortname }}
	            	</span>
	          	</div>
	          	<div class="specialsell_price">
	            	<span class="specialsell_price_text">{{ list.price }}疯抢</span>
	          	</div>
	        </li>
	        <div class="div_clear"></div>
      	</ul>
      <div class="div_clear"></div>
    </div>
</template>


<script>
	import toptitle from './toptitle.vue'

	export default{
		data(){
			return {
				toptitlemessage: {
					toptitleimg: require('./../../assets/pic07.png'),
					shoppingicon: require('./../../assets/sale.png'),
					toptitletext: '今日特卖',
					englishtext: 'CHEAP'
				},
				todaysellfood: [
					{
						name: '皇冠  曲奇',
						englishkind: 'COOKIES',
						todaysellfoodshowimg: require('./../../assets/sell_show_01.jpg'),
						todaysellfoodhideimg: require('./../../assets/sell_hide_01.jpg'),
						shortname: '曲奇',
						price: '19.9'
					},
					{
						name: '非凡  威化',
						englishkind: 'WAFER',
						todaysellfoodshowimg: require('./../../assets/sell_show_02.jpg'),
						todaysellfoodhideimg: require('./../../assets/sell_hide_02.jpg'),
						shortname: '威化卷',
						price: '40.9'
					},
					{
						name: '红心木瓜8斤',
						englishkind: 'PAWPAW',
						todaysellfoodshowimg: require('./../../assets/sell_show_03.jpg'),
						todaysellfoodhideimg: require('./../../assets/sell_hide_03.jpg'),
						shortname: '水果',
						price: '50.9'
					},
					{
						name: '夹心饼干',
						englishkind: 'BISCUIT',
						todaysellfoodshowimg: require('./../../assets/sell_show_04.jpg'),
						todaysellfoodhideimg: require('./../../assets/sell_hide_04.jpg'),
						shortname: '夹心饼干',
						price: '16.9'
					}
				]
			}
		},
		components: {
			toptitle
		}
	}
</script>


