						goodsname: '友臣  肉松饼整箱  月饼饼干',
						goodsclassify: '肉松饼',
						marketprice: '9.70',
						presentprice: '8.80'
					},
					{
						goodspic: require('@/assets/goods_07.jpg'),
						goodsname: 'Richeese  丽芝士 雅嘉奶酪味玉米棒（膨化食品） 印度尼西亚进口',
						goodsclassify: '威化',
						marketprice: '9.90',
						presentprice: '9.00'
					},
					{
						goodspic: require('@/assets/goods_08.jpg'),
						goodsname: '丹夫  华夫饼（提拉米苏味)',
						goodsclassify: '华夫饼',
						marketprice: '9.90',
						presentprice: '8.90'
					}
				]
			}
		},
		created(){
			this.productgoods = this.bestsellgoods
		},
		mounted() {
			// 自定义方法
			eventBus.$on('change-message',() => {
				this.bestsellgoods = this.$root.bestsellgoods
			})
		},
		components: {
			foodproductsnav,
			pricesort,
			foodproducts
		}
	}
</script>


